    ShowOpenCard:function(dataPos, cardList){
        let uiPos = this.DDZRoomPosMgr.GetUIPosByDataPos(dataPos);
        if(uiPos == 0) return;
        this.LogicDDZGame.SortCardByMax(cardList);
        let openCardList = this.node.getChildByName("openCardList" + uiPos);
        openCardList.active = true;
        for(let i = 0; i < openCardList.children.length; i++){
            let cardNode = openCardList.children[i];
            let value = cardList[i];
            let isLastCard = false;
            if (i == cardList.length - 1) {
                isLastCard = true;
            }
            let isShowLandowner = false;
            if (this.landowner == dataPos && isLastCard) {
                isShowLandowner = true;
            }
            if(value){
                this.ShowCard(value, cardNode,isLastCard,isShowLandowner);
            }
            else{
                cardNode.active = false;
            }
        }
    },

