    //
    FaPaiAction:function(dataPos){
        let card = this.allCards.children[this.allCardIdx];
        let clientCards = this.DDZRoomSet.GetHandCard();
        this.SortCardByMax(clientCards);
        if(!card || !card.active){
            this.btn_openCard.active = false;
            this.FaPaiFinish();
            return;
        }
        let posList = this.DDZRoomMgr.GetEnterRoom().GetRoomProperty("posList");
        
        if(dataPos >= posList.length){
            dataPos = 0;
        }

        let uiPos = this.DDZRoomPosMgr.GetUIPosByDataPos(dataPos);
            
        this.allCardIdx++;
        if (this.allCardIdx <= 18) {
            this.btn_openCard.getChildByName("lb_multiple").getComponent(cc.Label).string = "x4";
            this.openCardMutiple = 4;
        } else if (this.allCardIdx > 18 && this.allCardIdx <= 36) {
            this.btn_openCard.getChildByName("lb_multiple").getComponent(cc.Label).string = "x3";
            this.openCardMutiple = 3;
        } else {
            this.btn_openCard.getChildByName("lb_multiple").getComponent(cc.Label).string = "x2";
            this.openCardMutiple = 2;
        }
        
        let position = null;
        if(dataPos == this.DDZRoomPosMgr.GetClientPos()){
            position = this.handCards.getPosition();
        }
        else{
            let node = this.GetWndNode("sp_seat0" + uiPos);
            position = node.getPosition();
        }
        let setInfo = this.DDZRoomSet.GetRoomSetInfo();
        let posInfo = setInfo.posInfo;
        let self = this;
        let action = cc.sequence(cc.moveTo(0.025, position), cc.callFunc(function(card){
            if(self.DDZRoomPosMgr.GetClientPos() == dataPos){
                self.handCards.active = true;
                let cardNode = self.handCards.children[self.allPlayerCardIdx[0]]
                cardNode.y = 0;
                let cardValue = clientCards[self.allPlayerCardIdx[0]];
                let isLastCard = false;
                if (self.allPlayerCardIdx[0] == clientCards.length - 1) {
                    isLastCard = true;
                }
                self.ShowCard(cardValue, cardNode, isLastCard);
                self.allPlayerCardIdx[0] += 1;
            }
            else if (self.DDZRoomPosMgr.GetClientPos() != dataPos && 
                posInfo[dataPos].openCard == 1) {
                let openCardList = [];
                self.AddCardAction(dataPos);
                let allOpenCardList = posInfo[dataPos].cards;
                self.openCardInfo[dataPos] = posInfo[dataPos].cards;
                let uiPos = self.DDZRoomPosMgr.GetUIPosByDataPos(dataPos);
                for (let i = 0; i < self.allPlayerCardIdx[uiPos]; i++) {
                    openCardList.push(allOpenCardList[i]);
                }
                console.log(" " + dataPos + " :" + openCardList);
                self.ShowOpenCard(dataPos, openCardList);
            }
            else{
                self.AddCardAction(dataPos);
            }
            dataPos++;
            card.active = false;
            self.FaPaiAction(dataPos);
            self.SoundManager.PlaySound("giveCard",self.DDZDefine.SoundPath + "giveCard");
        }));
        card.runAction(action);
    },
