    AddHead:function(){
        //  
        let posList = this.DDZRoomMgr.GetEnterRoom().GetRoomProperty("posList");
        let playerCount = posList.length;
        for(let idx = 0; idx < posList.length; idx++){
            let uiPos = this.DDZRoomPosMgr.GetUIPosByDataPos(posList[idx].pos);
            let path = "sp_seat0"  + uiPos.toString() + "/head";
            let node = this.GetWndNode(path);
            if(!node.getChildByName("UIDDZHead" + uiPos)){
                let head = cc.instantiate(this.headPrefab);
                node.addChild(head);
               
                let headScript = head.getComponent("UIDDZHead");
                headScript.Init(uiPos, posList[idx].pos, cc.v2(0,0), this.Left[uiPos]);
            }
            else{
                let headNode = node.getChildByName("UIDDZHead" + uiPos);
                // headNode.getChildByName("otherNode").active = false;
                // headNode.getChildByName("otherNode").getChildByName("bsPrefab").active = false;
                let headScript = headNode.getComponent("UIDDZHead");
                headScript.Init(uiPos, posList[idx].pos, cc.v2(0,0), this.Left[uiPos]);
            }
        }
    },

